.categories {
    min-height: 300px;
    width: 100%;
    padding: 40px 0px 40px 0px;

    h1.title {
        font-family: 'Poppins';
        font-weight: 600;
        font-size: 35px;
        text-align: center;
        margin-bottom: 30px;
    }

    .container {
        .categoriesContainer {

            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            margin: 0 auto;
            padding: 0 0px;

            .category {
                padding-right: 15px;
                margin: 10px 15px;
                border-radius: 5px;
                width: calc(33.3333% - 30px);
                height: 72px;
                display: flex;
                align-items: center;
                flex-direction: row;
                background-color: white;
                border: 1px solid lightgray;
                text-decoration: none !important;
                color: black;

                @media screen and (max-width:1200px) {
                    width: calc(50% - 30px);
                }

                @media screen and (max-width:992px) {
                    width: calc(100% - 30px);
                }


                &:hover {
                    cursor: pointer;
                    background-color: #f5f5f5;
                }

                .cover {
                    margin-right: 15px;

                    img {
                        border-radius: 5px;
                        width: calc(70px * (16 / 9));
                        height: 70px;
                        object-fit: cover;
                        border-right: 1px solid lightgray;
                    }
                }

                .title {
                    font-family: inherit;
                    font-weight: bold;
                    text-decoration: none !important;
                }
            }
        }
    }
}